@charset "utf-8";

.header {
  width: 100%;
  height: 80px;
  background-color: #242f9b;
  color: #ffffff;
  display: flex;
  justify-content: space-between;
}

.sign-out-button {
  color: #ffffff;
  margin: auto 25px auto auto;
}
.header-h1 {
  margin-left: 4%;
}

@media screen and (max-width: 320px) {
  .header {
    display: block;
  }
  .sign-out-button {
    display: block;
  }
}
